import Web3 from 'web3';
import LP_ABI from './data/LP_ABI.json'
import axios from 'axios'

const LP_ADDRESS = '0xbf0b068b00BBeddFD37310A1fbb4035130453301'
const RPC = 'https://bsc-dataseed4.defibit.io'

const getWBTPrice = async() => {
    let web3Instance = new Web3(new Web3.providers.HttpProvider(RPC, {
        timeout: 3000
    }))
    let lpContract = new web3Instance.eth.Contract(LP_ABI, LP_ADDRESS)
    let reserved = await lpContract.methods.getReserves().call()
    let priceInBNB = reserved[0] / reserved[1]
    let {data} = await axios.get('https://api.binance.com/api/v3/ticker/price?symbol=BNBUSDT')
    return parseFloat (priceInBNB) * parseFloat(data.price)

            
};

export default {
    getWBTPrice
}