import React, { Component } from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faCopy } from '@fortawesome/free-solid-svg-icons';
import { Breadcrumb } from '@themesberg/react-bootstrap';
import Footer from "../components/Footer"
import { HashLink } from 'react-router-hash-link';
import { Card } from '@themesberg/react-bootstrap';

//import { TransactionsTable } from "../components/Tables";
import VoteTableComponent from '../components/VoteTable';


class Voting extends Component {
  onCoppy = () => {
    var copyText = document.getElementById("copy-example");
    var textArea = document.createElement("textarea");
    textArea.value = copyText.textContent;
    document.body.appendChild(textArea);
    textArea.select();
    document.execCommand("Copy");
    textArea.remove();
    document.getElementById("copied").textContent = "Copied";
    setTimeout(function(){
      document.getElementById("copied").textContent = "";
    }, 3000)
  }

  render() {
    return (
      <>
        <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
          <div className="d-block mb-4 mb-md-0">
            <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
              <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
              <Breadcrumb.Item>Home</Breadcrumb.Item>
              <Breadcrumb.Item active>Voting</Breadcrumb.Item>
            </Breadcrumb>
            <h4>Voting Rules</h4>
            <ul>
              <li>go to <a className="text-underline" rel="noopener noreferrer" target="_blank" href="https://t.me/waterfallbot_community_chat">@waterfallbot_community_chat</a> , comment your token there following format
                <br /><code>
                  TokenSymbol-Address-Blockchain
                </code>
                <br />
                example: <br/><code>
                  <span type="text" value="Hello World"  id="copy-example" >WBT-0xc7435088819F1AF950357fED65BE856165cC3abB-BSC</span>
                  <button onClick={this.onCoppy}><FontAwesomeIcon icon={faCopy} /></button>
                  <span id="copied" className="text-danger font-small"></span>
                </code>
              </li>
              <li>Bot will give you a link to waterfall channel</li>
              <li>Invite friends join that waterfall channel</li>
              <li>Reach 300 members, your token will be added to Voting event in <a className="text-underline" rel="noopener noreferrer" target="_blank" href="https://t.me/CENTRALwaterfall">@CENTRALwaterfall</a></li>
              <li>Get 300 votes to be listed on waterfall bot</li>
              <li>Only 1 winner per Voting event</li>
			 <li><span className="text-danger">Required: Join <a className="text-underline" rel="noopener noreferrer" target="_blank" href="https://t.me/CENTRALwaterfall">@CENTRALwaterfall</a></span></li>
            </ul>
			Want to have your bot in 15 min without any above effort with PAID listing. 
			<Card.Link as={HashLink} className="text-underline text-danger" to="/request">Click here</Card.Link>
          </div>
        </div>
        <VoteTableComponent />
        <Footer />
      </>
    );

  };
};

export default Voting


