
import React, { useState } from "react";
import SimpleBar from 'simplebar-react';
import { useLocation } from "react-router-dom";
import { CSSTransition } from 'react-transition-group';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faAd, faRobot, faStar, faList, faCoins, faVoteYea, faRocket } from "@fortawesome/free-solid-svg-icons";
import { Nav, Badge, Image, Button, Navbar } from '@themesberg/react-bootstrap';
import { Link } from 'react-router-dom';

import { Routes } from "../routes";
import WaterfallBotIcon from "../assets/img/technologies/waterfall.svg";

// Google analytics
import ReactGA from 'react-ga';
ReactGA.initialize('UA-199733388-1');
const sendGA = (category, action) => {
  ReactGA.event({
    category: category,
    action: action,
  });

}

export default (props = {}) => {
  const location = useLocation();
  const { pathname } = location;
  const [show, setShow] = useState(false);
  const showClass = show ? "show" : "";

  const onCollapse = () => setShow(!show);

  const NavItem = (props) => {
    const { title, link, external, target, icon, image, badgeText, badgeBg = "secondary", badgeColor = "primary" } = props;
    const classNames = badgeText ? "d-flex justify-content-start align-items-center justify-content-between" : "";
    const navItemClassName = link === pathname ? "active" : "";
    const linkProps = external ? { href: link } : { as: Link, to: link };

    return (
      <Nav.Item className={navItemClassName} onClick={() => setShow(false)}>
        <Nav.Link {...linkProps} target={target} className={classNames}>
          <span>
            {icon ? <span className="sidebar-icon"><FontAwesomeIcon icon={icon} /> </span> : null}
            {image ? <Image src={image} width={20} height={20} className="sidebar-icon svg-icon" /> : null}

            <span className="sidebar-text">{title}</span>
          </span>
          {badgeText ? (
            <Badge pill bg={badgeBg} text={badgeColor} className="badge-md notification-count ms-2">{badgeText}</Badge>
          ) : null}
        </Nav.Link>
      </Nav.Item>
    );
  };

  return (
    <>
      <Navbar expand={false} collapseOnSelect variant="dark" className="navbar-theme-primary px-4 d-md-none">
        <Navbar.Brand className="me-lg-5" as={Link} to={Routes.Presentation.path}>
          <Image src={WaterfallBotIcon} className="navbar-brand-light" />
        </Navbar.Brand>
        <Navbar.Toggle as={Button} aria-controls="main-navbar" onClick={onCollapse}>
          <span className="navbar-toggler-icon" />
        </Navbar.Toggle>
      </Navbar>
      <CSSTransition timeout={300} in={show} classNames="sidebar-transition">
        <SimpleBar className={`collapse ${showClass} sidebar d-md-block bg-primary text-white`}>
          <div className="sidebar-inner px-4 pt-3">
            <Image src={WaterfallBotIcon} className="navbar-brand-light" width={64} height={64} />
            <Nav className="flex-column pt-3 pt-md-0">
              <NavItem title="Home" link={Routes.Presentation.path} icon={faHome} />
              <NavItem title="All Bots" icon={faList} link={Routes.Channels.path} onClick={() => sendGA('All Bots', 'Side Menu')} />
              <NavItem title="Token" icon={faCoins} link={Routes.Token.path} onClick={() => sendGA('Token', 'Side Menu')} />
              <NavItem title="Roadmap" icon={faRocket} link={Routes.Roadmap.path} onClick={() => sendGA('Roadmap', 'Side Menu')} />
              <NavItem title="VIP" icon={faStar} link={Routes.VIP.path} onClick={() => sendGA('VIP', 'Side Menu')} />
              <NavItem title="Request Price bot" icon={faRobot} link={Routes.RequestBot.path} onClick={() => sendGA('Request Bots', 'Side Menu')} />
              <NavItem title="Advertisement" icon={faAd} link={Routes.Advertisement.path} onClick={() => sendGA('Advertisement', 'Side Menu')} />
            </Nav>
          </div>
        </SimpleBar>
      </CSSTransition>
    </>
  );
};
